.about-me{
  position: absolute;
  top: 50vh;
  left: 50%;
  width: 40%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  font-family: 'Royal';
  font-size: 1.8vw;
  color: white;
  z-index: 9999;
  @media screen and (max-width: 1024px) {
    top: 50vh;
    left: 5%;
    font-size:  2vw;
  }
  @media screen and (max-width: 768px) {
    width: 92vw;
    left: 3%;
    font-size: 5.8vw;
    @media screen and (max-height: 480px) {
      font-size: 2.5vw;
    }
  }
  @media screen and (max-width: 480px) {
    // font-size:  4.6vw;
  }
  p{
    background: rgba(240, 240, 240, 0.14);
    padding: 3%;
    border-radius: 4px;
    @media screen and (max-width: 768px) {
      @media screen and (max-height: 768px) {
        padding: 1.6%;
      }
    }
    b{
      line-height: 22px;
    }
    span{
      margin-top: 1em;
      a{
        text-decoration: none;
        color: rgb(194, 215, 241);
        font-weight: bold;
        text-shadow: 2px 2px rgba(1, 9, 80, 0.87);
      }
    }
  }
  p:nth-child(2){
    margin-top: 2.4%;
    @media screen and (max-width: 1024px) {
      margin-top: 4%;
    }
  }
}
